* {
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
}
.flex-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
}
.container {
  width: 100%;
}
.left {
  padding: 10px;
  margin: 10px;
  width: 35%;
  -ms-flex-positive: 1;
      flex-grow: 1;
}
.right {
  padding: 10px;
  margin: 10px;
  width: 60%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 100%;
}
textarea {
  font-size: 1.2em;
  background: #f2f2f2;
  width: 100%;
  max-width: 100%;
  height: 80%;
  font-family: monospace;
  padding: 10px;
}
.left > h2, .right > h2 {
  color: #4d4d4d;
  font-size: 2.5em;
}
table, table *{
  border: solid;
  border-color: #cecece;
  border-collapse: collapse;
  border-width: 1px;
  padding: 10px;
}
table {
  margin: 20px;
}
tr:nth-child(even) {
  background-color: #EDEDED;
}
pre, code {
  background-color: #eee;
  padding: 3px;
}
pre {
  padding: 10px;
}


